import {mapState} from 'vuex'
export default {
  computed:{
    ...mapState(['themes']),
    theme(){
      // console.log((this.$route).split("/")[1])
      return this.themes[ (this.$route.path).split("/")[1]]
    },
    navbarStyle(){
      return {
        width: this.$route.path=='/'? "350px":"450px",
        right: this.$route.path!='/expo/all'?"0px":"calc(100% - 450px)"
      }
    }
  }
}



